body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

h1, h2 {
  text-align: center;
  font-family: Roboto, sans-serif;
}

.editorContainer {
  flex-direction: row;
  justify-content: center;
  margin-top: 32px;
  margin-bottom: 64px;
  display: flex;
}

.codeEditor {
  width: 40%;
  height: 600px;
  background-color: #f0f0f0;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}

.testResults {
  width: 30%;
  height: 584px;
  background-color: #e6e6e6;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  flex-direction: column;
  justify-content: start;
  padding: 8px;
  display: flex;
}

.testResultsTitle {
  align-self: center;
  margin-top: 10px;
  margin-bottom: 5px;
  font-family: Roboto, sans-serif;
  font-size: 150%;
}

.runButton {
  height: 2em;
  width: 5em;
  color: #fff;
  cursor: pointer;
  text-shadow: none;
  box-shadow: none;
  background-color: #1aaa55;
  border: 1px solid #168f48;
  border-radius: 4px;
  align-self: flex-end;
  margin-top: auto;
  font-family: Roboto, sans-serif;
  font-size: 120%;
  transition: all .2s ease-in-out;
}

.runButton:disabled {
  background-color: #727272;
  border-color: #444;
}

.runButton:hover:enabled {
  background-color: #199e50;
}

.runButton:active:enabled {
  color: #d6d6d6;
  background-color: #137a3e;
}

.testResult {
  height: 1.8em;
  background-color: #cfcfcf;
  border-radius: 4px;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  margin: .5vh;
  padding: .5vh;
  display: flex;
}

.testPassed {
  color: #fff;
  height: 1.05em;
  width: 2em;
  background-color: #308d1d;
  border-radius: 4px;
  margin: .5vh;
  padding: .5vh 1vw .5vh .7vw;
  font-family: Roboto, sans-serif;
  font-weight: bold;
}

.testFailed {
  color: #fff;
  height: 1.05em;
  width: 2em;
  background-color: #be1818;
  border-radius: 4px;
  margin: .5vh;
  padding: .5vh 1vw;
  font-family: Roboto, sans-serif;
  font-weight: bold;
}

.testSkipped {
  color: #fff;
  height: 1.05em;
  width: 2em;
  background-color: #8c8f00;
  border-radius: 4px;
  margin: .5vh;
  padding: .5vh 1vw;
  font-family: Roboto, sans-serif;
  font-weight: bold;
}

.testName {
  padding-left: 1vw;
  font-family: Roboto, sans-serif;
}

.failureMessage {
  padding: 8px;
  font-family: Roboto, sans-serif;
  font-size: 80%;
}

.errorToast {
  height: 4em;
  width: 30%;
  background-color: #aa2626;
  border: 1px solid #790000;
  border-radius: 4px;
  transition: all .2s ease-in-out;
  position: fixed;
  bottom: 10%;
  left: 35%;
  box-shadow: 0 0 16px #888;
}

.hiddenErrorToast {
  opacity: 0;
}

.closeToast {
  color: #fff;
  cursor: pointer;
  height: 1.5em;
  width: 1.5em;
  background-color: #e00000;
  border: 1px solid #790000;
  border-radius: 4px;
  font-size: 150%;
  transition: all .2s ease-in-out;
  position: absolute;
  top: .6em;
  right: .6em;
}

.closeToast:hover:enabled {
  background-color: #b60101;
}

.closeToast:active:enabled {
  color: #d6d6d6;
  background-color: #a00101;
}

.toastMessage {
  color: #fff;
  padding-top: 20px;
  padding-left: 40px;
  font-family: Roboto, sans-serif;
  font-size: 120%;
  position: absolute;
}

.description {
  background-color: #f7f7f7;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 32px;
  padding: 32px;
  display: flex;
}

.descriptionText {
  text-align: center;
  font-family: Roboto, sans-serif;
  font-size: 200%;
}

.mainFeatures {
  flex-direction: row;
  justify-content: center;
  margin-left: 32px;
  margin-right: 32px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.mainFeatures > p:first-line {
  color: #621c5f;
  font-size: 150%;
  font-weight: bold;
}

.featureIcon {
  color: #621c5f;
  padding-bottom: 20px;
}

.code {
  color: #168f48;
  font-family: Courier New, Courier, monospace;
}

footer {
  color: #cacaca;
  text-align: center;
  background-color: #252525;
  padding-top: 16px;
  padding-bottom: 16px;
  font-family: Roboto, sans-serif;
}

.socialMedia {
  padding-left: 32px;
}

.socialMediaBadge {
  color: #cacaca;
  padding-left: 10px;
  font-size: 150%;
  transition: all .2s ease-in-out;
}

.socialMediaBadge:hover {
  color: #fff;
}

